import qs from "query-string";
import { testId } from "../helpers/const";

let tg = window.Telegram.WebApp;
let chatId = tg.initDataUnsafe;
const userId = chatId?.user?.id ?? testId;
const user = chatId?.user;
const first_name = user?.first_name;
const last_name = user?.last_name;
const username = user?.username;

window.initDataUnsafe = tg.initDataUnsafe;

const Api = {

  hostname: 'https://botrazbor.ru/telegram', 
  get : async function(url, params){
    
        params = {
          ...params,
          telegram_id: userId,
          first_name: first_name,
          last_name: last_name,
          username: username,
        };
        const res = await fetch(`${this.hostname}${url}?${qs.stringify(params)}`,{    
            method: 'GET',    
            withCredentials: true,    
            crossorigin: true,    
            mode: 'cors',       
          });
          return await res.json();
    }, 
    post : async function(url, params){

        const res = await fetch(`${this.hostname}${url}`,{    
            method: 'POST',    
            withCredentials: true,    
            crossorigin: true,
            mode: 'cors',
            body: params
          });
          const result = await res.json();
        return result;
    }, 

    delete : async function(url, params){
      const res = await fetch(`${this.hostname}${url}`,{    
        method: 'DELETE',    
        withCredentials: true,    
        crossorigin: true,
        headers: {
          'Content-Type': `application/json`
        },
        mode: 'cors',
        body: JSON.stringify(params),
      });

        const result = await res.json();
      return result;
  }, 
}

export default Api;
