const initValuesHeader = {
    title: ''
}

export const getActionHeader = data => ({type: 'TITLE', title: data})



export const header = (state = initValuesHeader, action ) => {

    switch(action.type){
        case 'TITLE':
            return ({
                ...state,
                title: action.title
            });
        default: return state ;
    }
}

