import { StyleSheet, Text, View, SafeAreaView, Image, Dimensions } from "react-native"
import { Pressable } from "react-native-web"



const ItemMainMenu = ({
    item,
    bgColor,
    colorText,
    handlerChangeScreen,
}) => {
    console.log({item})
    return(
        <SafeAreaView style={[styles.container, {backgroundColor: bgColor}]}>
            <Pressable
                style={styles.menuBottom}
                onPress={()=>handlerChangeScreen(item.slug)}
            >
                <Image                
                    style={{
                        width: 44,
                        height: 44,
                        backgroundColor: 'rgba(228, 58, 62, 0.2)',
                    }}
                    resizeMode="cover"
                    source={{
                        uri: item.image
                    }}
                />
                <Text
                    style={[styles.menuTextBottom, {color: colorText}]}
                >{item.name}</Text>
            </Pressable>
        </SafeAreaView>
    )
}


export default ItemMainMenu;

