import { combineReducers, createStore } from 'redux';
import { header } from './header/header';
import { mainMenu } from './mainMenu/mainMenu';

function counter(state = 0, action) {
    switch (action.type) {
        case 'INCREMENT':
            return state + 1;
        case 'DECREMENT':
            return state - 1;
        default:
            return state;
    }
}

const tg = (state = {tg:{}}, action ) => {
    return ({
        ...state,
        tg: window?.Telegram?.WebApp
    })
}

const initStateColorSchame = {
    bgColorHeader: '#222327',
    bgColorFone: '#131116',
    bgColorFoneMenu: '#222327',
    bgColor: window?.Telegram?.WebApp?.themeParams?.bg_color ?? '#222327',
    textColor: window?.Telegram?.WebApp?.themeParams?.text_color ?? '#ffffff',
}
const colorTheme  = (state = initStateColorSchame, action) => {

    switch(action.type){
        case 'COLOR-B-G':
            return ({
                ...state,
                bgColor: action?.color ?? window?.Telegram?.WebApp?.themeParams?.bg_color
            })
        case 'COLOR-TEXT':
            return ({
                ...state,
                textColor: action?.color ?? window?.Telegram?.WebApp?.themeParams?.text_color
            })
        default: return state;
    }
}

export const changeCount = (data) => ({ type: 'INCREMENT', data })
export const store = createStore(combineReducers({
    colorTheme,
    mainMenu,
    counter,
    header,
    tg,
}))