import * as React from 'react';
import { View, Text, SafeAreaView, FlatList, StyleSheet, VirtualizedList, Dimensions } from 'react-native';

import { connect } from 'react-redux';
import { changeCount } from '../../store/store'
import ItemMainMenu from '../UI/ItemMainMenu';
import BottonMenu from '../UI/BottomMenu';
import { defaultStyles } from '../styles';

function MenuScreen({
  bgColor,
  mainMenu,
  colorText,
  navigation,
  bgColorFone,
}) {
  const handlerChangeScreen = (url) => {
    navigation.replace(url)
  }
  return (
    <SafeAreaView style={[styles.container, {backgroundColor: bgColorFone}]}>
       <View style={[defaultStyles.body, {marginBottom: 100}]} >
        
        <FlatList
          data={mainMenu.filter(el=>el.id !== 999)}
          numColumns={2}
          columnWrapperStyle={{
            justifyContent: 'space-around',
            marginBottom: 25,
            width: '100%',
            display: 'flex',
          }}
          renderItem={({ item }) => <ItemMainMenu item={item} bgColor={bgColor} colorText={colorText} handlerChangeScreen={handlerChangeScreen} />}
          keyExtractor={item => item.id}
          />
      </View>


      <View style={defaultStyles.bottomBottom} >
        <BottonMenu listBottom={mainMenu} navigation={navigation} bgColor={bgColor} />
      </View>
    </SafeAreaView>
  );
}

const mapStateToProps = state => {
  return {
    colorText: state.colorTheme.textColor,
    bgColor: state.colorTheme.bgColor,
    mainMenu: state.mainMenu?.buttonMainMenu,
    bgColorFone: state.colorTheme.bgColorFone,
    bgColorFoneMenu: state.colorTheme.bgColorFoneMenu,
  }
}
const mapDispatchToProps = {
  changeCount,

}
const MenuScreenConteiner = connect(mapStateToProps, mapDispatchToProps)(MenuScreen)
export default MenuScreenConteiner;



const styles = StyleSheet.create({
  container: {
    flex: 1,
    paddingTop:16,
  },
  title: {
    fontSize: 32,
  },
});

