import { StyleSheet, Text, View, FlatList, Image } from "react-native"
import { Pressable } from "react-native-web"



const MenuItem = ({
    item,
    handlerChangeScreen,
}) => {

    return(
        <Pressable
            onPress={()=>handlerChangeScreen(item.slug)}
        >
            <Image                
                style={{
                    width: 30,
                    height: 30,
                    marginRight: 15,
                }}
                resizeMode="cover"
                source={{
                    uri: item.image
                }}
            />
        </Pressable>
    )
}

const BottonMenu = ({
    bgColor,
    listBottom,
    navigation,
}) => {
    console.log('go to ', {navigation})
    const handlerChangeScreen = (url) => {
        console.log('go to ', url, { navigation })
        navigation.replace(url)
      }
    return (
        <View style={[styles.container, {backgroundColor: bgColor}]}>
            
            <FlatList
                // horizontal
                centerContent={true}
                numColumns={10}
                columnWrapperStyle={{
                    justifyContent: 'space-around',
                    width: '100%'
                }}
                data={listBottom}
                renderItem={({item}) => <MenuItem item={item} handlerChangeScreen={handlerChangeScreen} />}
                keyExtractor={item => item.id}  
            />
            <Text >
            </Text>
        </View>
    )
}

export default BottonMenu;

const styles= StyleSheet.create({
    container:{
        padding: 0,
        display: 'flex',
        bottom: 0,
        justifyContent: 'center ',
        width: '100%',
        alignItems: 'center',        
        height: '100%',
        width: '100%'
    },
    menuBottom: {
        flexDirection: 'row',
        paddingHorizontal: 10,
        flex: 1,
    }
})