import * as React from 'react';
import { View, Text, SafeAreaView, VirtualizedList, StyleSheet, Pressable, Dimensions } from 'react-native';
import { connect } from 'react-redux';
import { changeCount } from '../../store/store'
import BottonMenu from '../UI/BottomMenu';
import { defaultStyles } from '../styles';

function CreateRequest({
  count,
  changeCount,
  bgColor,
  mainMenu,
  colorText,
  navigation,
  bgColorFone,
}) {
  const handlerChangeScreen = (url) => {
    console.log('go to ', url,{navigation})
    navigation.replace(url)
  }

  const Item = ({title}) => (
    <View style={styles.item}>
      <Text style={styles.title}>{title}</Text>
    </View>
  );
  const getItemCount = _data => 150;
  const getItem = (_data, index) => ({
    id: Math.random().toString(12).substring(0),
    title: `Item ${index + 1}`,
  });

  return (
    <SafeAreaView style={[styles.container, {backgroundColor: bgColorFone}]}>
        
        <View style={[defaultStyles.body, {marginBottom: 100}]} >
            <Pressable
              onPress={() => {
                console.log('test click count')
                handlerChangeScreen('/')}
                }>
              <Text>click {count}</Text>
            </Pressable>


            <VirtualizedList
                initialNumToRender={100}
                renderItem={({item}) => <Item title={item.title} />}
                keyExtractor={item => item.id}
                getItemCount={getItemCount}
                getItem={getItem}
            />
        </View>

        <View style={defaultStyles.bottomBottom} >
        <BottonMenu listBottom={mainMenu} navigation={navigation} bgColor={bgColor} />
        </View>

    </SafeAreaView>
  );
}

const mapStateToProps = state => {
  console.log({ state })
  return {
    // count: state?.counter ?? 0,
    colorText: state.colorTheme.textColor,
    bgColor: state.colorTheme.bgColor,
    bgColorFone: state.colorTheme.bgColorFone,
    mainMenu: state.mainMenu?.buttonMainMenu,
  }
}
const mapDispatchToProps = {
  changeCount,
}
const CreateRequestConteiner = connect(mapStateToProps, mapDispatchToProps)(CreateRequest)
export default CreateRequestConteiner;



const styles = StyleSheet.create({
  container: {
    flex: 1,    
  },  
  item: {
    backgroundColor: '#f9c2ff',
    height: 150,
    justifyContent: 'center',
    marginVertical: 8,
    marginHorizontal: 16,
    padding: 20,
  },
  title: {
    fontSize: 32,
  },
});

