import { home } from "../../images";

const LIST_MAIN_MENU = 'LIST-MAIN-MENU';
const initStateMainMenu = {
    buttonMainMenu: [ {
        action_page_id: null,
        additional_info: "unit",
        id: 999,
        image: `${home}`,
        name: "/",
        parent_page_id: 1,
        slug: '/',
        text: "Главная",
        class: '',
        can_click: {
            message: "test",
            redirect_payment: true,
            status: true,
        },
        },]
};

export const mainMenu = (state = initStateMainMenu, action ) => {
    console.log({action})
    

    switch(action.type){

        case LIST_MAIN_MENU:
            const res = {
                ...state,
                buttonMainMenu: [...state.buttonMainMenu, ...action.buttons]
            };
            return res;
        default: return state;
    }
}


export const mainMenuListAction = (data, dispatch ) => {
    console.log({dispatch})
    return ({type: LIST_MAIN_MENU, ...data})
}