import * as React from 'react';
import { StyleSheet, Text, View, Dimensions, SafeAreaView, Alert, Modal, Pressable } from 'react-native';

import cogoToast from 'cogo-toast';
import { home } from '../../images';
import ModalWindowPromt from '../UI/ModalWindowPromt';
import { connect } from 'react-redux';
import { getActionHeader } from '../../store/header/header';
import { changeStateBackButton, setDataButtonBottonTrigger } from '../../helpers';
import { mainMenuListAction } from '../../store/mainMenu/mainMenu';
import Api from '../../api';
import MenuScreenConteiner from '../MenuScreen/MenuScreen';
import { createStackNavigator } from '@react-navigation/stack';
